*{
margin:0;
padding:0;
box-sizing: border-box;
}
body{
padding: auto;
  font-family: cursive;
}
.app{
  
background-image: linear-gradient(
  90deg,
  rgba(20, 159, 255, 1) 0%,
  rgba(17, 122, 255, 1) 100%
);
transition: 0.4 ease;

}

.app.warm{
 
  background-image: linear-gradient(to right top, #be2d2d, #c6512e, #cc6e34, #d18941, #d5a254);
  transition: 0.4 ease;
}

main{
  min-height: 100vh;
}

.search-box{
padding-top:6vh;
 text-align: center; 
}
.search-bar{
width: 320px;
height: 50px;
border-radius: 0 0 10px 10px;
font-family:sans-serif; 
border: none;
outline: none;
padding-left: 30px;
font-size: 18px;
background-color: rgb(255,255,255,0.4);
box-shadow:0px 5px rgba(199, 129, 129, 0.2);

}
.search-bar:focus {
  background-color: rgb(255,255,255,0.75);
  box-shadow: 0px 5px rgba(199, 129, 129, 0.4);
}
.location-box{
padding:3%;
text-align: center;
margin-left: auto;
margin-right: auto;
}
.location{
 
  color:blanchedalmond;
  font-size: 32px;
  font-weight: 500;
  text-shadow: 3px 2px rgba(69, 69, 97, 0.5);
}
.date{
 color:blanchedalmond;
 font-size: 20px;
 font-weight: 300;
 text-shadow: 3px 2px rgba(59, 50, 50, 0.1); 
}
.weather-box{
  padding:5vh;
  margin-left: auto;
  margin-right: auto;
  background-color: rgba(255,255,255,0.1);
  box-shadow: 5px 8px 8px 10px rgba(0,0,0,0.5);
  transition: 0.s;
  width:300px;
  border-radius:8px;
}
.temp{
  color: white;
  font-size: 102px;
  text-shadow: 3px 6px rgba(50,50,50,0.5);
}
.weather{
  color:white;
  font-size:35px;
  font-weight: 200;
  text-shadow: 3px 3px  rgba(50,50,50,0.5);
}

.weather-instruction{
  margin-left: auto;
  margin-right: auto;
  padding: 4vh;
  color: aliceblue;
  font-size: 18px;
  font-weight: 200;
  transition: 0.s;
  width:300px;
}

.weather-box i{
color: white;  
font-size:50px ;
}